module.exports = "<div class=markdown><h1 id=popover>Popover</h1> <hr> <p>点击/鼠标移入元素，弹出气泡式的卡片浮层。</p> <h2 id=->何时使用</h2> <p>当目标元素有进一步的描述和相关操作时，可以收纳到卡片中，根据用户的操作行为进行展现。</p> <p>和 <code>Tooltip</code> 的区别是，用户可以对浮层上的元素进行操作，因此它可以承载更复杂的内容，比如链接或按钮等。</p> <h2 id=api>API</h2> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>trigger</td> <td>触发行为，可选 <code>hover/focus/click</code></td> <td>string</td> <td>hover</td> </tr> <tr> <td>placement</td> <td>气泡框位置，可选 <code>top/left/right/bottom</code></td> <td>string</td> <td>top</td> </tr> <tr> <td>title</td> <td>卡片标题</td> <td>String</td> <td>无</td> </tr> <tr> <td>content</td> <td>卡片内容</td> <td>String</td> <td>无</td> </tr> <tr> <td>overlayClassName</td> <td>卡片类名</td> <td>string</td> <td>无</td> </tr> <tr> <td>overlayStyle</td> <td>卡片样式</td> <td>object</td> <td>无</td> </tr> <tr> <td>visible</td> <td>用于手动控制浮层显隐</td> <td>boolean</td> <td>false</td> </tr> </tbody> </table> <h2 id=->组件演示</h2> <div class=code-boxes> <div class=code-box> <h3>基本</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-popover title=title content=contentcontentcontentcontentcontent> <v-button type=primary>Hover me</v-button> </v-popover> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-popover</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>title<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>contentcontentcontentcontentcontent<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</sp